}

.mob-icon-position {
  position: absolute;
  top: 6px;
  left: 14px;
}

.mobile__menu-wraper {
  width: 100%;
  align-items: center;
  display: none;
}

.logo {
  color: #111321;
  cursor: pointer;
  color: #111321;
  padding-left: 16px;
  padding-right: 16px;
  font-family: Manrope, sans-serif;
  font-size: 24px;
  font-style: normal;
  font-weight: 700;
  line-height: 33px;
}

.mobile__menu-btn {
  width: 30px;
  height: 30px;
  z-index: 50;
  cursor: pointer;
  margin-left: auto;
  display: block;
  position: relative;
  overflow: hidden;
}

@media screen and (min-width: 768px) {
  .mobile__menu-btn {
    display: none;
  }
}

.mobile__menu-btn.isMenuOpen {
  background-color: var(--color-bg);
  opacity: 1;
  transition: opacity 1s cubic-bezier(.4, 0, .2, 1), transform .6s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 28px;
  right: 16px;
  transform: translateY(0%);
}

.mobile__menu-btn span {
  width: 18px;
  height: 2px;
  background-color: var(--header-text-color);
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.mobile__menu-btn span:nth-of-type(2) {
  top: calc(50% - 4px);
}

.mobile__menu-btn span:nth-of-type(3) {
  top: calc(50% + 4px);
}

.mobile__menu {
  background-color: var(--color-bg);
  width: 100vw;
  height: 100vh;
  opacity: 0;
  padding-top: 24px;
  padding-bottom: 18px;
  transition: opacity .9s ease-in, transform .9s ease-in;
  position: fixed;
  left: 0;
  transform: translateY(-120%);
}

@media screen and (min-width: 768px) {
  .mobile__menu {
    display: none;
  }
}

.mobile__menu-list {
  flex-direction: column;
  gap: 24px;
  margin-top: 68px;
  display: flex;
}

.mobile__menu.isMenuOpen {
  background-color: var(--color-bg);
  width: 100vw;
  height: 100vh;
  background-color: var(--color-bg);
  z-index: 2;
  opacity: 1;
  transition: all .9s ease-in-out;
  position: fixed;
  top: 0;
  transform: translateY(0%);
}

.mobile__menu-item {
  height: 44px;
  align-items: center;
  gap: 13px;
  list-style-type: none;
  display: flex;
}

.mobile__menu-link {
  color: var(--header-text-color);
  width: 100%;
  font-family: Manrope, sans-serif;
  font-size: 16px;
  font-style: normal;
  font-weight: 500;
  line-height: 22px;
  display: block;
}

.mobile__menu-item.active {
  background-color: #4440f6;
}

.mobile__menu-item.active .mobile__menu-link {
  color: #f4f4f4;
}

.mobile__menu-item.active .mobile__menu-icon, .mobile__menu-item.active .mobile__menu-icon__arrow {
  stroke: #f4f4f4;
}

.mobile__menu-icon {
  stroke: none;
  margin-left: 16px;
}

.mobile__menu-icon__arrow {
  fill: none;
  margin-left: auto;
  margin-right: 16px;
}

.mobile__menu-btn.isMenuOpen span:nth-of-type(1) {
  display: none;
}

.mobile__menu-btn.isMenuOpen span:nth-of-type(2) {
  top: 50%;
  transform: translate(-50%)rotate(45deg);
}

.mobile__menu-btn.isMenuOpen span:nth-of-type(3) {
  top: 50%;
  transform: translate(-50%)rotate(-45deg);
}

.container-mobile {
  height: 100%;
