}

.mobile__menu-btn > span {
  pointer-events: none;
}

:root {
  --mainBgcolor: #f4f4f4;
  --countCard: 1;
  --gapCard: 30px;
}

#categories-show {
  white-space: nowrap;
  column-gap: 16px;
  margin: 0;
  padding: 0;
  display: flex;
  overflow: hidden;
}

#categories-hide {
  width: max-content;
  scroll-behavior: smooth;
  background-color: #f8f8f8;
  flex-direction: column;
  row-gap: 1px;
  margin: 0;
  padding: 0 10px 0 0;
  display: flex;
  overflow-y: scroll;
}

#categories-hide .categories__item {
  text-align: start;
  cursor: pointer;
  background-color: #f8f8f8;
  border: none;
  padding: 5px;
}

#categories-hide .categories__item:hover, #categories-hide .categories__item:focus {
  background-color: inherit;
  color: #4b48db;
  text-decoration-line: underline;
}

#categories-hide .categories__item.activ {
