}

.filter {
  width: 100%;
  height: 100%;
  z-index: 3;
  background: #f8f8f8;
  flex-direction: column;
  margin: 0;
  padding: 20px 0 20px 10px;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
  top: 35px;
  left: 0;
  transform: translateY(-201%);
}

.filter-wrapper {
  pointer-events: none;
  width: 150px;
  height: 300px;
  z-index: 5;
  filter: drop-shadow(0 10px 60px #0003);
  background: none;
  border-radius: 20px;
  padding: 5px;
  position: absolute;
  overflow: hidden;
}

.filter-show {
  pointer-events: all;
  opacity: 1;
  top: 0;
  left: 0;
  transform: translateY(0);
}

.filter__btn span {
  margin-right: 10px;
  font-family: Manrope, sans-serif;
}

.categories__icon.rotate {
  transform: rotate(-180deg);
}

.categories__icon {
  fill: currentColor;
  transition: transform .25s cubic-bezier(.4, 0, .2, 1);
  transform: rotate(0);
}

.categories__item, .filter__btn {
  color: #4b48db;
  background-color: var(--bthBgColor);
  text-align: center;
  cursor: pointer;
  border: 1px solid #4b48db;
  border-radius: 20px;
  align-items: center;
  margin: 0;
  padding: 7px 22px;
  font-family: Manrope, sans-serif;
  font-size: 12px;
  font-weight: 400;
  line-height: 1.3334;
  transition: background-color .25s cubic-bezier(.4, 0, .2, 1), border-color .25s cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.categories__item.activ, .categories__item:hover, .categories__item:focus, .filter__btn.activ, .filter__btn:hover, .filter__btn:focus {
